<template>
  <div class="fade-out fade-in h-100 w-100 bg-dark landing-view" sticky></div>
</template>

<script>
export default {
  data() {
    return {
      show: true
    }
  },
  mounted() {
    document.getElementById('breadcrumb').style['visibility'] = 'hidden';
    setTimeout(() => {
      this.show = false;
      document.getElementById('breadcrumb').style['visibility'] = 'visible';
    }, 2000);
  }
}
</script>

<style>
.landing-view {
  background-image: url("@/assets/IDMS.png");
  background-repeat: no-repeat;
  background-position: center;
  z-index: 3002;
  position: fixed;
  top: 0;
  left: 0;
}
</style>